import $ from '../core';

// Функция табов
$.prototype.tab = function() {
    for (let i = 0; i < this.length; i++) {
        $(this[i]).on('click', () => {
            // добавляем Класс активности Текущему табу, Убираем у всех Соседних табов
            // ищем родительский блок (tab) и внутри него Ищем Контент Табов - Убираем Класс активности у всего Контента, и Добавляем Класс активности Текущему Контенту через eq( который такой же как и индекс самого Таба)
            $(this[i])
                .addClass('tab-item-active').siblings().removeClass('tab-item-active')
                .closest('.tab').find('.tab-content').removeClass('tab-content-active')
                .eq($(this[i]).index()).addClass('tab-content-active');
        });
    }
};

$('[data-tabpanel] .tab-item').tab();