import $ from '../core';

// Модальные окна
$.prototype.modal = function(created) {
    for (let i = 0; i <this.length; i++) {
        const target = this[i].getAttribute('data-target');
        $(this[i]).click((e) => {
            e.preventDefault();
            $(target).fadeIn(300);
            document.body.style.overflow = 'hidden';
        });

        const closeElements = document.querySelectorAll(`${target} [data-close]`);
        closeElements.forEach(elem => {
            $(elem).click(() => {
                $(target).fadeOut(300);
                document.body.style.overflow = '';
                // если окно создано при помощи скрипта - после его закрытия надо удалить его, чтобы не налкладывались окна друг на друга
                if (created) {
                    document.querySelector(target).remove();
                }
            });
        });

        // закрытие по подложке (#modalwindow)
        $(target).click(e => {
            if (e.target.classList.contains('modal')) {
                $(target).fadeOut(300);
                document.body.style.overflow = '';
                if (created) {
                    document.querySelector(target).remove();
                }
            }
        });
    }
};

// инициализируем функцию (если вёрстка сразу помещена на страницу)
$('[data-toggle="modal"]').modal();

// Функция Динамической модалки
// ({динамические параметры, могут быть разные кнопки, текст и тп, передаваться могут в разном порядке и имеют дефолтное значение пустоты})  
$.prototype.createModal = function({text, btns} = {}) {
    // перебираем все элементы вызова модалки
    for (let i = 0; i < this.length; i++) {
        let modal = document.createElement('div');
        modal.classList.add('modal');
        modal.setAttribute('id', this[i].getAttribute('data-target').slice(1)); // убрали # из id
        
        // Работа с кнопками модалки (их может быть разное количество - не шаблонное)
        const buttons = [];
        // btns = {count: num, settings: [[text, classNames=[], close, callback]]}
        for (let j = 0; j < btns.count; j++) {
            let btn = document.createElement('button');
            // динамически добавляем ...множество классов
            btn.classList.add('btn', ...btns.settings[j][1]); // 1 - className
            // добавляем текст кнопки
            btn.textContent = btns.settings[j][0];
            // если close = true - добавляем атрибут data-close
            if (btns.settings[j][2]) {
                btn.setAttribute('data-close', 'true');
            }
            // если callback функция существует - задаём через click её
            if (btns.settings[j][3] && typeof(btns.settings[j][3]) === 'function') {
                btn.addEventListener('click', btns.settings[j][3]);
            }

            // в массив кнопок закидываем все сформированные кнопки
            buttons.push(btn);
        }

        modal.innerHTML = `
        <div class="modal-dialog">
            <div class="modal-content">
                <button class="close" data-close>
                    <span>&times;</span>
                </button>
                <div class="modal-header">
                    <div class="modal-title">
                        ${text.title}
                    </div>
                </div>
                <div class="modal-body">
                    ${text.body}
                </div>
                <div class="modal-footer">
                    
                </div>
            </div>
        </div>
        `;

        // чтобы кнопки правильно работали - через append их закидываем в футер
        modal.querySelector('.modal-footer').append(...buttons);
        // закидываем модальное окно в Вёрстку
        document.body.appendChild(modal);
        // вызываем функцию модалки (создание самого окна (createModal) в main.js)
        $(this[i]).modal(true);  // (created) - позволит устранить накладывание модалок др на др, так как, если created = true - удаляются модалки просто
        $(this[i].getAttribute('data-target')).fadeIn(300);
    }
};