
// ФАЙЛ ДОБАВЛЕНИЯ Свойств Отображения
import $ from '../core';

// Создаём функционал (show - отобразит объект)
$.prototype.show = function() {
    for(let i = 0 ;i < this.length; i++) {
        // проверка - применяется ли к объекту свойства style
        if (!this[i].style) {
            continue;
        }
        this[i].style.display = '';  // браузер выберет сам какой display
    }

    // возвращаем объект для дальнейшей работы
    return this;
}

// hide - спрячет объект
$.prototype.hide = function() {
    for(let i = 0 ;i < this.length; i++) {
        if (!this[i].style) {
            continue;
        }
        this[i].style.display = 'none'; 
    }

    // возвращаем объект для дальнейшей работы
    return this;
}

// переключение отображение объекта
$.prototype.toggle = function() {
    for(let i = 0 ;i < this.length; i++) {
        if (!this[i].style) {
            continue;
        }
        if (this[i].style.display === 'none') {
            this[i].style.display = '';
        }  else {
            this[i].style.display = 'none';
        }
    }

    // возвращаем объект для дальнейшей работы
    return this;
}