
// Файл добавления Действий к Объекту

import $ from '../core';

// Функция замены/показа содержимого
$.prototype.html = function(content) {
    for (let i = 0; i < this.length; i++) {
        if(content) {
            // если передаём контент - заменяем
            this[i].innerHTML = content;
        } else {
            // если контент не передан - получаем содержимое
            return this[i].innerHTML;
        }
    }

    return this;
};

// Функция выбора Определённого ЭЛЕМЕНТА
$.prototype.eq = function(i) {
    // получаем нужный элемент
    const swap = this[i];
    // получаем длину всего объекта
    const objLength = Object.keys(this).length; // keys трансформирует в Массив (можно исп length)
    for (let i = 0; i < objLength; i++) {
        // очищаем весь объект
        delete this[i]
    }
    // помещаем нужный элемент на первую позицию Объекта и ВОЗВРАЩАЕМ ОБЪЕКТ
    this[0] = swap;
    this.length = 1;
    return this;

};

// Функция получения Порядкового Номера элемента у конкретного Родителя
$.prototype.index = function() {
    const parent = this[0].parentNode;
    const childs = [...parent.children];  // помещаем в массив всех детей (через spread оператор)

    const findMyIndex = (item) => {
        return item == this[0];
    };

    // среди детей возвращаем индекс элемента который мы сравнили с искомым в findmyIndex 
    return childs.findIndex(findMyIndex);
};

// Функция поиска Элементов по определённому селектору(selector) Внутри Селектора (объекта)
$.prototype.find = function(selector) {
    let numberOfItems = 0;  // общее кол-во элементов по селектору
    let counter = 0;       // кол-во элементов записанных в this

    const copyObj = Object.assign({}, this);  // Копия нашего объекта this 

    for (let i = 0; i < copyObj.length; i++) {
        // ищем среди нашего объекта Нужный Селектор
        const arr = copyObj[i].querySelectorAll(selector);
        // если не нашли, то пропускаем
        if (arr.length == 0) {
            continue;
        }
        // если что-то нашли - записываем это в объект с индексом counter 
        for (let j = 0; j < arr.length; j++) {
            this[counter] = arr[j];
            counter++;
        }

        // кол-во элементов массива селектора
        numberOfItems += arr.length;
    }

    this.length = numberOfItems;

    // НЕОБХОДИМО ИЗБАВИТЬСЯ ОТ ХВОСТА (то есть был объект из 5 элементов, наш селектор выбрал 3 из них, они перезаписались в {}, однако там остались два элемента которые нам не нужны - их надо обрезать)
    // перебираем все элементы по селектору ДО длины всего объекта 
    for (; numberOfItems < Object.keys(this).length; numberOfItems++) {
        delete this[numberOfItems];  // удаляем оставшийся хвостик
    };

    // Возвращаем объект
    return this;
};

// Функция ближайшего элемента
$.prototype.closest = function(selector) {
    let counter = 0;  // кол-во ближайших элементов

    for (let i = 0; i < this.length; i++) {
        this[i] = this[i].closest(selector);
            counter++;
    }

    const objLength = Object.keys(this).length; // keys трансформирует в Массив (можно исп length)
    for (; counter < objLength; counter++) {
        // удаляем из объекта те элементы, которые совпали с counter (ближайшие)
        delete this[counter];
    }

    return this;
};

// Функция по получению Соседей нужного Элемента внутри Родительского блока (ДЛЯ ТАБОВ)
$.prototype.siblings = function() {
    let numberOfItems = 0;  // общее кол-во соседей
    let counter = 0;       // кол-во элементов записанных в this

    const copyObj = Object.assign({}, this);  // Копия нашего объекта this 

    for (let i = 0; i < copyObj.length; i++) {
        // ищем всех соседей у элементов внутри объекта
        const arr = copyObj[i].parentNode.children;
        // НЕОБХОДИМО исключить тот ЭЛЕМЕНТ у которого мы ИЩЕМ СОСЕДЕЙ
        for (let j = 0; j < arr.length; j++) {
            // если внутри объекта есть элемент из массива соседей (мы пропускаем его)
            if (copyObj[i] === arr[j]) {
                continue;
            }

            // и записываем в наш объект с индексом (counter) - всех соседей, БЕЗ НАШЕГО ЭЛЕМЕНТА
            this[counter] = arr[j];
            counter++;
        }

        // кол-во элементов массива соседей ( -1 т.к. выкинули Наш элемент)
        numberOfItems += arr.length - 1;
    }

    this.length = numberOfItems;

    // НЕОБХОДИМО ИЗБАВИТЬСЯ ОТ ХВОСТА 
    // перебираем всех соседей ДО длины всего объекта 
    for (; numberOfItems < Object.keys(this).length; numberOfItems++) {
        delete this[numberOfItems];  // удаляем оставшийся хвостик из Объекта
    };

    // Возвращаем объект
    return this;
};