

// Функционал по добавлению Анимаций

import $ from '../core';

// Функция ИНИЦИАЛИЗАЦИИ анимации
$.prototype.animateOverTime = function(duration, callback, final) { // duration - время вып. анимации, callback - функция которая выполняться будет(fadein и тп) final - запустится после конца анимации
    let timeStart;    // стартовое время

    // Техническая функция ОПРЕДЕЛЕНИЯ ВРЕМЕНИ И ИЗМЕНЕНИЯ АНИМАЦИИ. Для request animation frame (функция обращается к браузеру и просит его запустить себя перед перерисовкой браузера КАЖДЫЕ ПАРУ МИЛЛИСЕКУНД КОТОРАЯ ПРОИСХОДИТ)
    function _animateOverTime(time) {
        if (!timeStart) {
            timeStart = time;     // стартовое время (time - автоматический опред. браузером - минимальное значение пары миллисекунд, увеличивается каждые пару миллисекунд т.к. requestAnimationFrame - вызывается с такой периодичностью)
        }
        
        let timeProgress = time - timeStart;  // прошедшее время анимации
        let complection = Math.min(timeProgress / duration, 1); // изменение параметров на странице (прозрачность: делим прошедшее время анимации на длительность анимации, и если оно переваливает за 1, то функция min будет подставлять второй аргумент 1 - максимальное значение opacity)

        callback(complection); // функция изменения прозрачности

        // если анимация ещё не кончилась запускаем тех.функцию
        if (timeProgress < duration) {
            requestAnimationFrame(_animateOverTime); // функция requestAnimationFrame обращается к браузеру и просит его запустить себя перед перерисовкой браузера, которая происходит КАЖДЫЕ ПАРУ МИЛЛИСЕКУНД (передаём внутрь техническую функцию)
        } else { 
            // запускается final если анимация закончилась
            if (typeof final === 'function') {
                final();
            }
        }
    }

    // возвращаем тех.функцию, для дальнейшего использования
    return _animateOverTime;
};

// Функция fadeIn
$.prototype.fadeIn = function(duration, display, final) {
    for (let i = 0; i < this.length; i++) {
        this[i].style.display = display || 'block'; // 'block' - парам. по умолчанию

    // тех.функция (происходит каждый пару миллисекнд) - это callback из функции Инициализации
        const _fadeIn = (complection) => {      // в иниц.фун. в complection изменяется opacity
            this[i].style.opacity = complection;
        };

        const anim = this.animateOverTime(duration, _fadeIn, final);
        requestAnimationFrame(anim);
    }

    // возвращаем объект
    return this;
};

// Функция fadeOut
$.prototype.fadeOut = function(duration, final) {
    for (let i = 0; i < this.length; i++) {

    // тех.функция (происходит каждый пару миллисекнд) - это callback из функции Инициализации
        const _fadeOut = (complection) => {
            this[i].style.opacity = 1 - complection; // элем. становится прозрачнее и прозрачнее, так как тех. функции запускаются каждые парум миллисекунд и следовательно complection увеличивается до 1 (и в итоге opacity будет 0)
            if (complection === 1) {            // если opacity = 0 - скрываем элементы
                this[i].style.display = 'none';
            }
        };

        const anim = this.animateOverTime(duration, _fadeOut, final);
        requestAnimationFrame(anim);
    }

    // возвращаем объект
    return this;
};

// Toggle fade
$.prototype.fadeToggle = function (duration, display, final) {
    for (let i =0; i < this.length; i++) {
        // через getComputedStyle, так как стили могут задавать через классы, и нужно обращаться к ним как-то
        if (window.getComputedStyle(this[i]).display === 'none') {
            this[i].style.display = display || 'block';

            const _fadeIn = (complection) => {
                this[i].style.opacity = complection;
            };
            const anim = this.animateOverTime(duration, _fadeIn, final);
            requestAnimationFrame(anim);
            
        } else {
            const _fadeOut = (complection) => {
                this[i].style.opacity = 1 - complection; 
                if (complection === 1) {        
                    this[i].style.display = 'none';
                }
            };
            const anim = this.animateOverTime(duration, _fadeOut, final);
            requestAnimationFrame(anim);
        }
    }

    return this;
};