import $ from '../core';

// СЛАЙДЕР В ЛИНИЮ
$.prototype.carousel = function() {
    for (let i = 0; i < this.length; i++) {
        // слайдер будет inline типа = нужна ширина блока слайдера (окошко)
        const width = window.getComputedStyle(this[i].querySelector('.carousel-inner')).width;
        const slides = this[i].querySelectorAll('.carousel-item'); // слайды
        const slidesField = this[i].querySelector('.carousel-slides'); // полоска слайдов
        const dots = this[i].querySelectorAll('.carousel-indicators li'); // табы
        // ширина блока со слайдами (в линию идут - виден один слайд)
        slidesField.style.width = 100 * slides.length + '%';
        // даём слайдам ширину окошка
        slides.forEach(slide => {
            slide.style.width = width;
        });

        
        let offset = 0;      // переменная для отслеживания смещения слайда (влево или вправо)
        let slideIndex = 0;  // номер слайда

        // кнопка следующий слайд
        $(this[i].querySelector('[data-slide="next"]')).click((e) => {
            e.preventDefault();
            // ПОСЛЕДНИЙ СЛАЙД: если отступ = (числовой тип данных ширины слайда без % и px * кол-во слайдов) то отступ = 0 (первый слайд)
            if (offset == (+width.replace(/\D/g, '') * (slides.length -1))) {
                offset = 0
            // иначе к отступу добавляем ширину слайда - двигаемся вперёд
            } else {
                offset += +width.replace(/\D/g, '');
            }

            // сдвигаем полоску слайдов на offset влево (поэтому -)
            slidesField.style.transform = `translateX(-${offset}px)`;
            
            // увеличиваем номер слайда
            if (slideIndex == slides.length - 1) {
                slideIndex = 0;
            } else {
                slideIndex++;
            }

            // класс активности для табов
            dots.forEach(dot => dot.classList.remove('active'));
            dots[slideIndex].classList.add('active');
        });

        // кнопка предыдущий слайд
        $(this[i].querySelector('[data-slide="prev"]')).click((e) => {
            e.preventDefault();
            // ПЕРВЫЙ СЛАЙД: подставляем значение отступа который равен длине всех слайдов => переместимся на последний слайд
            if (offset == 0) {
                offset = +width.replace(/\D/g, '') * (slides.length -1);
            // иначе отнимаем ширину слайда - передвигаемся назад
            } else {
                offset -= +width.replace(/\D/g, '');
            }

            // сдвигаем полоску слайдов на offset вправо (- потому что offset отрицательный)
            slidesField.style.transform = `translateX(-${offset}px)`;

            // уменьшаем номер слайда
            if (slideIndex == 0) {
                slideIndex = slides.length - 1;
            } else {
                slideIndex--;
            }

            // класс активности для табов
            dots.forEach(dot => dot.classList.remove('active'));
            dots[slideIndex].classList.add('active');
        });
        
        // переключение слайдов по Табам
        $(`.carousel-indicators li`).click(e => {
            const slideTo = e.target.getAttribute('data-slide-to'); // data-slide-to - содержит номер слайда
            slideIndex = slideTo;
            offset = +width.replace(/\D/g, '') * slideTo; 
            slidesField.style.transform = `translateX(-${offset}px)`;
            dots.forEach(dot => dot.classList.remove('active'));
            dots[slideIndex].classList.add('active');
        });
    }
};

$('.carousel').carousel();