import $ from '../core';

// Функция аккордиона (раскрывающийся контент)
$.prototype.accordion = function(headActive = 'accordion-head-active', contentActive = 'accordion-content-active', paddings = 40) { // 40 т.к. по 20 отступы у текста в scss
    for (let i = 0; i < this.length; i++) {
        $(this[i]).click(() => {
            $(this[i]).toggleClass(headActive);
            $(this[i].nextElementSibling).toggleClass(contentActive);

            // показываем Контент если у Таба есть класс Активности
            if (this[i].classList.contains(headActive)) {
                // увеличиваем maxheight у Контента на ту высоту КОТОРАЯ У НЕГО ДОЛЖНА БЫТЬ (scrollHeight) + ПЭДДИНГИ
                this[i].nextElementSibling.style.maxHeight = this[i].nextElementSibling.scrollHeight + paddings + "px";
            } else {
                this[i].nextElementSibling.style.maxHeight = "0px";
            }
        });
    }
};

$('.accordion-head').accordion();