
// ФАЙЛ СОЗДАНИЯ СВОИХ ФУНКЦИЙ 

const $ = function (selector) {
    // возвращаем Объект на основе init, для использования МЕТОДОВ этого объекта
    return new $.prototype.init(selector);
};

$.prototype.init = function(selector) {
    if (!selector) {
        return this;  // {} -- пустой объект
    }

    // если у селектора есть tagName - значит это Html элемент => ПОМЕЩАЕМ ЕГО В НАЧАЛО ОБЪЕКТА и ВОЗВРАЩАЕМ ОБЪЕКТ
    if (selector.tagName) {
        this[0] = selector;
        this.length = 1;
        return this;
    }

    Object.assign(this, document.querySelectorAll(selector)); // asign - в сущ. объект добавляет новые свойства (наш селектор добавит)
    this.length = document.querySelectorAll(selector).length;
    return this;   // ВОЗВРАЩАЕМ ОБЪЕКТ С НАШИМ СЕЛЕКТОРОМ
};

// !!! В ПРОТОТИП ОБЪЕКТА (который возвращается в функции init) ЗАПИСЫВАЕМ ПРОТОТИП НАШЕЙ ГЛАВНОЙ ФУНКЦИИ !!!
// ЭТО ДАЁТ ВОЗМОЖНОСТЬ ПРИ ИСПОЛЬЗОВАНИИ ФУНКЦИИ $ (она возвращает объект, который позволяет использовать методы) - использовать ЛЮБЫЕ ФУНКЦИИ ОБЪЕКТА который мы ВЕРНУЛИ, т.е удаление, добавление классов и тп
$.prototype.init.prototype = $.prototype; // (грубо говоря методы в прототипе Объекта $ = методам в прототипе Возвращённого объекта из функции init)

window.$ = $; // передали значение нашей функции $ клавише $

// Экспортируем для использования в файлах
export default $;


